import React from 'react';
import { useState } from 'react';

const Init = (props) => {
    const [savedText, setSavedText] = useState()
    function handleClick() {
        const userText = document.getElementById("userText");
        setSavedText(userText.value);
        setSavedText(crypto.randomUUID())
        
        navigator.storage.estimate().then((estimate)=>console.log(estimate))
      }
    async function listRoot() {
        console.log(`listRoot: `)
        for await (const [key, value] of props.opfsRoot.entries()) {
            console.log({ key, value });
        }
    }
    async function deleteEntry() {    
        await props.opfsRoot.removeEntry("draft.txt")
    }
    async function createEntry() {    
        await props.opfsRoot.getFileHandle("draft.txt", { create: true })
    }
    async function getFile() {
        // Open file picker and destructure the result the first handle
        const [fileHandle] = await window.showOpenFilePicker();
        const file = await fileHandle.getFile();
        return file;
    }
    return (
        <>
            <label htmlFor="userText">userText (4 to 8 characters):</label>
            <input type="text" id="userText" name="userText" required minLength="4" maxLength="8" size="10" />
            <p>{savedText}</p>
            <button onClick={listRoot}>
                List
            </button>
            <button onClick={createEntry}>
                create
            </button>
            <button onClick={deleteEntry}>
                remove
            </button>
            <button onClick={getFile}>
                browse
            </button>
        </>
    );
};

export default Init;